@use "vars" as *;

#header{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background: white;
	z-index: 999;
	padding: 20px 220px 20px 20px;
	@include flex(0,center,wrap);
	@include mq(tb){
		padding: 11px 15px 10px;
	}
	.header-logo{
		display: block;
		width: 160px;
	}

	.header-nav{
		width: calc(100% - 200px);
		padding: 0 0 0 6%;
		@include mq(tb-min){
			//display: flex !important;
			//justify-content: flex-end;
			//align-items: center;
			//flex-wrap: wrap;
			display: block !important;
			opacity: 1 !important;
			transition: all 0s ease !important;
		}
		@include mq(tb){
			display: none;
			position: absolute;
			top: 50px;
			left: 0;
			right: 0;
			width: 100%;
			height: calc(100vh - 50px);
			padding: 0 20px 60px;
			overflow-y: scroll;
			background: rgba(#0081CCCC,0.8);
			&.active{
				display: block;
			}
		}
		&__menu{
			display: flex;
			flex-wrap: wrap;
			align-items: flex-end;
			@include mq(tb){
				display: block;
			}
			&-item{
				position: relative;
				padding: 0 20px;
				@include mq(){
					padding: 0;
					&:nth-child(1){
						a{
							padding: 11px 0 11px 20px;
						}
					}
				}
				a{
					display: block;
					@include font-size(16);
					color: #333;
					position: relative;
					@include mq(tb){
						@include font-size(20);
						font-weight: 600;
						color: white;
						border-bottom: 1px dotted white;
						padding: 15px 0 15px 20px;
						&:before{
							content: "";
							border: solid white;
							border-width: 0 1px 1px 0;
							display: inline-block;
							padding: 3px;
							transform: translateY(-50%) rotate(-45deg);
							position: absolute;
							top: 50%;
							left: 0;
						}
						.c-ft--en{
							font-weight: 400;
						}
						.c-ft--jp{
							@include font-size(17);

						}
					}

				}
			}
		}
	}
	.js-mobile {
		width: 50px;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		cursor: pointer;
		z-index: 12;
		background: #0081CC;
		display: none;
		@include mq(tb){
			@include flex(center,center);
		}
		span{
			position: absolute;
			@include v-align;
			display: block;
			width: 16px;
			height: 2px;
			background: white;
			border-radius: 3px;
			&:before,&:after{
				position: absolute;
				left: 0;
				width: 100%;
				height: 100%;
				background: white;
				content: '';
				-webkit-transition: -webkit-transform 0.3s;
				transition: transform 0.3s;
				border-radius: 3px;
			}
			&:before{
				-webkit-transform: translateY(-250%);
				transform: translateY(-250%);
				top: -1px;
			}
			&:after{
				-webkit-transform: translateY(250%);
				transform: translateY(250%);
				bottom: -1px;
			}
		}
		&--close {
			span{
				background-color: transparent;
				&:before,&:after{
					width: 100%;
					background: white;
				}
				&:before {
					-webkit-transform: translateY(0) rotate(45deg);
					transform: translateY(0) rotate(45deg);
					top: 1px;
				}
				&:after {
					-webkit-transform: translateY(0) rotate(-45deg);
					transform: translateY(0) rotate(-45deg);
					bottom: -1px;
				}
			}
		}
	}
	.box-mail{
		position: absolute;
		@include v-align;
		right: 20px;
		@include mq(tb){
			border-radius: 0;
			height: auto;
			width: 50px;
			top: 0;
			right: 50px;
			bottom: 0;
			transform: translateY(0);
			span{
				display: none;
			}
		}
	}
}

.box-mail{
	background: #0081CC;
	width: 182px;
	height: 50px;
	color: white !important;
	@include flex(0,center,wrap);
	@include font-size(16);
	letter-spacing: 1px;
	font-weight: 600;
	line-height: 1.2;
	border-radius: 3px;
	position: relative;
	padding: 0 15px;
	@include mq(tb){
		background: #00AFEC;
	}
	img{
		width: 17px;
		display: block;
		margin: 0 15px 0 0;
	}
	span{
		border-left: 1px solid white;
		display: block;
		padding: 6px 0 6px 15px;
	}
}

.js-locked {
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
	body {
		-webkit-overflow-scrolling: auto;
	}
}