@use "vars" as *;


.c-fc{ //font-color
	&--white{
		color: white !important;
	}
	&--black{
		color: black !important;
	}
}

.c-bg{
	&--blue{
		background: $blue !important;
	}
	&--white{
		background: white !important;
	}
	&--dark{
		background: $dark !important;
	}
}


.c-ft{//font-family
	&--en{
		font-family: $font-en;
	}
	&--jp{
		font-family: $font-jp;
	}
}









.c-al{
	&--c{
		text-align: center !important;
	}
	
	&--r{
		text-align: right !important
	}
	
	&--l{
		text-align: left !important;
	}
}











/* ====================================================
Component
==================================================== */
// Common Text style
.c-txt{
	&__basic{
		@include fts(16,25.6);
		font-weight: 300;
	}
}




//ttl
.c-ttl{
	&__01{
		display: block;
		text-align: center;
		color: $blue;
		font-family: $font-en;
		@include font-size(40);
		font-weight: 400;
		line-height: 1.4;
		position: relative;
		margin-bottom: 52px;
		@include mq(){
			@include font-size(30);
			line-height: 1.2;
			letter-spacing: 1px;
		}
		.icon{
			display: block;
			margin-bottom: 10px;
		}
		.c-ft--jp{
			font-weight: 600;
			@include font-size(27);
			font-family: $font-gothic;
			@include mq(){
				@include font-size(21);
			}
		}
	}
	&__02{
		display: block;
		color: $blue;
		@include font-size(27);
		font-weight: 600;
		overflow: hidden;
		@include mq(){
			@include font-size(21);
			text-align: center;
		}
		span{
			display: inline-block;
			position: relative;
			letter-spacing: 5.5px;
			padding: 0 10px 0 0;
			margin-bottom: 69px;
			@include mq(){
				display: block;
				letter-spacing: 2px;
				padding: 0 10px 15px 0;
				margin-bottom: 35px;
			}
			&:after{
				@include mq(md-min){
					content: "";
					width: 2000px;
					height: 2px;
					background: $blue;
					position: absolute;
					@include v-align;
					left: 100%;
				}
				@include mq(){
					content: "";
					width: 20px;
					height: 3px;
					background: $blue;
					position: absolute;
					left: 0;
					right: 0;
					bottom: 0;
					margin: 0 auto;
				}
			}
		}
	}
}

//btn
.c-btn{
	&__01{

	}
}


//fade up
.js-scrollin {
	opacity: 0;
	-webkit-transform: translate(0, 70px);
	-ms-transform: translate(0, 70px);
	transform: translate(0, 70px);
	&.active{
		opacity: 1;
		-webkit-transform: translate(0, 0);
		-ms-transform: translate(0, 0);
		transform: translate(0, 0);
		-webkit-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
		-o-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
		transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	}
}



