@use "vars" as *;

#footer{
	background: $blue;
	color: white;
	padding: 43px 0 0;
	text-align: left;
	.inner{
		position: relative;
	}
	a{
		color: white;
	}
	.box-mail{
		position: absolute;
		bottom: 69px;
		right: 15px;
		@include mq(){
			display: none;
		}
	}
	.footer-block{
		position: relative;
		max-width: 858px;
	}
	.footer-logo{
		display: block;
		max-width: 277px;
		margin-bottom: 33px;
		@include mq(){
			width: 204px;
			margin: 0 auto 30px;
		}
	}
	.footer-sns{
		@include flex(0,center,wrap);
		margin-bottom: 23px;
		@include mq(){
			justify-content: center;
			margin-bottom: 30px;
		}
		li{
			margin-right: 16px;
			&:last-child{
				margin-right: 0;
			}
		}
	}
	.footer-menu{
		@include flex(0,0,wrap);
		@include mq(){
			display: none;
		}
		li{
			margin: 0 42px 20px 0;
			a{
				display: block;
				@include font-size(16);
				font-weight: 500;
				font-family: $font-jp;
				.c-ft--en{
					font-weight: 400;
				}
			}
		}
	}
	.copyright{
		text-align: center;
		background: #002962;
		@include font-size(12);
		font-weight: 500;
		padding: 15px;
		@include mq(){
			@include font-size(11);
		}
	}
}